@charset "UTF-8";
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-size: 50px; }

* {
  margin: 0;
  padding: 0; }

#content {
  height: 100%; }

.h100 {
  height: 100%; }

p {
  display: block;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px; }

/* 清理浮动 */
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.clearfix {
  zoom: 1; }
